<template>
  <v-container>
    <v-alert
      border="left"
      close-text="Close Alert"
      color="green accent-4"
      dark
      dismissible
      v-if="this.$route.params.message"
    >
      {{ this.$route.params.message }}
    </v-alert>
    <v-flex xs6 class="justify-center ml-5 mt-5">
      <v-autocomplete
        v-model="search"
        :items="posts"
        auto-select-first
        clearable
        dense
        filled
        solo
        solo-inverted
        label="Search for category"
        item-text="title"
        item-value="title"
        class="ml-5 aligns"
      ></v-autocomplete
    ></v-flex>

    <v-row no-gutters>
      <v-col sm="4" class="pa-3" v-for="post in filterednames" :key="post._id">
        <v-card class="pa-1" :to="{ name: 'Post', params: { id: post._id } }">
          <v-img height="250" :src="`/${post.image}`"></v-img>
          <v-btn class="ml-4 mt-3" small outlined color="indigo">
            {{ post.category }}
          </v-btn>
          <v-card-title class="headline">
            {{ post.title }}
          </v-card-title>
          <v-card-text class="py-0">
            <p>{{ post.content.substring(0, 100) + "..." }}</p>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-row>
      <v-card class="mx-auto" max-width="300" tile>
        <v-list shaped>
          <v-subheader>Category</v-subheader>
          <v-list-item-group v-model="selectedItem" color="primary">
            <v-list-item v-for="(post, i) in posts" :key="i">
              <v-list-item-content>
                <v-list-item-title v-text="post.category"></v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-list-item-group>
        </v-list>
      </v-card>
    </v-row>
  </v-container>
</template>
<script>
import API from "../api";
export default {
  name: "Home",
  data() {
    return {
      posts: [],
      search: "",
    };
  },
  async created() {
    this.posts = await API.getAllPost();
  },
  computed: {
    filterednames: function () {
      return this.posts.filter((post) => {
        return post.title.match(this.search);
      });
    },
  },
};
</script>
